.header a {
  display: flex;
  align-items: center;
  padding: 15px;
  color: #4a4a4a;
}

.header a:hover {
  background-color: #fafafa;
  color: #3273dc;
}

.app-container {
  /* background-color: #282c34; */
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

