html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F6F8FA;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  background-color: #fff;
}

.navbar-menu a {
  position: relative;
  padding: 10px 15px;
}

.navbar-menu .active::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #3273dc;
}

.navbar-brand {
  margin-right: 5px;
  width: 90px;
}

.navbar-brand img {
  width: 100%;
  height: 100%;
}

.container .container__header {
  font-size: 1.4em;
  font-weight: 600;
  color: #363636;
  margin-bottom: 20px;
  line-height: 1;
}

.card {
  border-radius: 5px;
  box-shadow: 0 9px 30px 0 rgba(35, 39, 42, .1) !important;
  -webkit-transition: all .2s cubic-bezier(.175, .885, .32, 1.275);
  transition: all .2s cubic-bezier(.175, .885, .32, 1.275);
}

.card:hover {
  box-shadow: 0 12px 30px 0 rgba(35, 39, 42, .1) !important;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.card-content {
  width: 100%;
  display: table;
}

.card-content .content {
  display: table-cell;
  vertical-align: top;
}

.content p span {
  font-weight: bold;
}

.card-content .content .detail-description {
  margin-bottom: 15px !important;
}

.container--people .card-content .content,
.container--location .card-content .content,
.container--species .card-content .content,
.container--vehicle .card-content .content {
  float: left;
}

.container--people .card-content .image,
.container--location .card-content .image,
.container--species .card-content .image,
.container--vehicle .card-content .image {
	width: 200px !important
}

.card-content .image {
  margin-right: 20px;
}

.card-content .content h1 {
  font-size: 1.8em !important;
	margin-bottom: 10px !important;
}

.card-content .content p {
  margin-bottom: 3px !important;
}

.image.is-128x128 {
  display: table-cell;
  float: left;
  height: 128px !important;
  width: 100px !important;
}

.image img {
  height: 100% !important;
  border-radius: 5px;
}

.container a {
  display: block;
}

.loader-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
  z-index: 10;
}
.header a {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px;
  color: #4a4a4a;
}

.header a:hover {
  background-color: #fafafa;
  color: #3273dc;
}

.app-container {
  /* background-color: #282c34; */
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

